import React, {Component} from 'react';
import { connect } from 'react-redux';
import Sidebar from '../components/Sidebar';
import { toggleClick } from '../actions/navbar';

const mapStateToProps = function(state, ownProps) {
  return {
    sidebarToggle: state.nav.sidebarToggle,
  };
};

const mapDispatchToProps = function(dispatch, ownProps) {
  return {
    toggleTemplate: (field) => dispatch(toggleClick(field))

  };
};

class SC extends Component {
  constructor(props) {
    super(props);
    this.state = {
      boardTemplateToggle: false,
      sidebarToggle      : false,
    };
  }


  render() {
    return (
      <Sidebar
        boardTemplateToggle={this.state.boardTemplateToggle}
        sidebarToggle={this.state.sidebarToggle}
        toggleClick={this.toggleClick}
      />
    );
  }
}
const SidebarContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(SC);

export default SidebarContainer;



